import React from "react";
import styled from "styled-components";

import { BoxContentCenter } from "../../0-base/Box";

const StyledHeaderBox = styled(BoxContentCenter).attrs({
  as: "header",
})`
  height: 64px;

  background-color: rgb(${(props) => props.theme.color.ultramarineBlue});

  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px rgba(0, 0, 0, 0.14), 0px 1px 10px rgba(0, 0, 0, 0.12);
`;

export default function HeaderBox(): JSX.Element {
  return <StyledHeaderBox />;
}
